import React from 'react';
import {NavLink} from "react-router-dom";

export const ProjectsPage = () => {
    return (
        <div className="projects-cont">
            <div className="content-header">
                <p>My Works</p>
            </div>
            <div className="content">
                <div className="box">
                    <p>CryptoBit</p>
                    <NavLink to="/cryptobit">
                        <img src="img/crypto.png" alt="cryptobit"/>
                    </NavLink>
                    <div className="box_footer">
                        <a href="https://github.com/mrKorj/CryptoBit" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-github"/>
                            &nbsp;GitHub repo</a>
                        <br/>
                        <span>CryptoBit this a SPA project created using: JavaScript, jQuery and Bootstrap.</span>
                    </div>
                </div>
                <div className="box">
                    <p>ToDo App</p>
                    <a href="#">
                        <img src="img/todo.png" alt="todo_app"/>
                    </a>
                    <div className="box_footer">
                        <a href="https://github.com/mrKorj/Todo-App" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-github"/>
                            &nbsp;GitHub repo</a>
                        <br/>
                        <span>Todo App, this a Full Stack project created using: ReactJS and Materialize css.
                This App include server side, NodeJS + Express and DataBase.
                </span>
                    </div>
                </div>
                <div className="box">
                    <p>Task Board</p>
                    <NavLink to="taskboard">
                        <img src="img/task-board.png" alt="task_board"/>
                    </NavLink>
                    <div className="box_footer">
                        <a href="https://github.com/mrKorj/Task-board" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-github"/>
                            &nbsp;GitHub repo</a>
                        <br/>
                        <span>Task Board this a project created using: JavaScript and CSS.</span>
                    </div>
                </div>
                <div className="box">
                    <p>Internet Store</p>
                    <NavLink to="/store">
                        <img src="img/store.png" alt="pic"/>
                    </NavLink>
                    <div className="box_footer">
                        <a href="https://github.com/mrKorj/Internet-Store" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-github"/>&nbsp;GitHub repo</a>
                        <br/>
                        <span>Internet Store this a project created using: Pure HTML and CSS.</span>
                    </div>
                </div>
            </div>
        </div>
    )
}