import React from 'react';
import {NavBar} from "./components/Navbar";
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import {HomePage} from "./pages/HomePage";
import {AboutPage} from "./pages/AboutPage";
import {ProjectsPage} from "./pages/ProjectsPage";
import {CryptoBit} from "./components/CryptoBit";
import {TaskBoard} from "./components/TaskBoard";
import {InternetStore} from "./components/InternetStore";

function App() {
    return (
        <BrowserRouter>
            <div className="main">
                <NavBar/>
                <Switch>
                    <Route path="/" exact>
                        <HomePage/>
                    </Route>
                    <Route path="/about">
                        <AboutPage/>
                    </Route>
                    <Route path="/projects">
                        <ProjectsPage/>
                    </Route>
                    <Route path="/cryptobit">
                        <CryptoBit/>
                    </Route>
                    <Route path="/taskboard">
                        <TaskBoard/>
                    </Route>
                    <Route path="/store">
                        <InternetStore/>
                    </Route>
                </Switch>
            </div>
        </BrowserRouter>
    );
}

export default App;
