import React from "react";
import {NavLink} from 'react-router-dom'

export const NavBar = () => {
    return (
        <nav>
            <div className="header-container">
                <div className="logo">
                    <p>mr.Korj</p>
                    <span>Full Stack Web Developer.</span>
                </div>
                <div>
                    <div className="menu">
                        <div>
                            <NavLink to="/"><i className="fas fa-home"/> Home</NavLink>
                        </div>
                        <div>
                            <NavLink to="/projects">Works</NavLink>
                        </div>
                        <div>
                            <NavLink to="/about">About</NavLink>
                        </div>
                    </div>
                </div>
                <div className="social">
                    <a href="https://www.facebook.com/kremenchugsky" target="_blank" rel="noopener noreferrer"><i
                        className="fab fa-facebook"/></a>
                    <a href="https://linkedin.com/in/sergy-kremenchugsky-31644b159" target="_blank"
                       rel="noopener noreferrer">
                        <i className="fab fa-linkedin-in"/></a>
                    <a href="https://github.com/mrKorj" target="_blank"
                       rel="noopener noreferrer"><i className="fab fa-github"/></a>
                    <a href="mailto:mr.korj@gmail.com"><i className="fas fa-at"/></a>
                </div>
            </div>
        </nav>
    )
}